/**********************
* Base style
**********************/
html,
body {
  height: 100%;
  width: 100%;
}

body {
  background: #fff;
  font-family: 'AkzidenzGroteskPro', sans-serif;
  font-size: 10px;
  color: #000;
  font-weight: 400;
  min-width: 320px;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  position: relative;
}

ul {
  list-style: none;
  padding: 0;
}

br {
  font-size: 0;
}

.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  box-sizing: border-box;
}

/*********************** typography **********************/
@font-face {
  font-family: "AkzidenzGroteskPro";
  src: url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Light.eot");
  src: url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Light.eot?#iefix") format("embedded-opentype"), url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Light.woff") format("woff"), url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Light.ttf") format("truetype");
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: "AkzidenzGroteskPro";
  src: url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Regular.eot");
  src: url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Regular.eot?#iefix") format("embedded-opentype"), url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Regular.woff") format("woff"), url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "AkzidenzGroteskPro";
  src: url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Bold.eot");
  src: url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Bold.eot?#iefix") format("embedded-opentype"), url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Bold.woff") format("woff"), url("../../fonts/AkzidenzGroteskPro/AkzidenzGroteskPro-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: "ProximaNova";
  src: url("../../fonts/ProximaNova/ProximaNova-Light.eot");
  src: url("../../fonts/ProximaNova/ProximaNova-Light.eot?#iefix") format("embedded-opentype"), url("../../fonts/ProximaNova/ProximaNova-Light.woff") format("woff"), url("../../fonts/ProximaNova/ProximaNova-Light.ttf") format("truetype");
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: "ProximaNova";
  src: url("../../fonts/ProximaNova/ProximaNova-Regular.eot");
  src: url("../../fonts/ProximaNova/ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../../fonts/ProximaNova/ProximaNova-Regular.woff") format("woff"), url("../../fonts/ProximaNova/ProximaNova-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "ProximaNova";
  src: url("../../fonts/ProximaNova/ProximaNova-Bold.eot");
  src: url("../../fonts/ProximaNova/ProximaNova-Bold.eot?#iefix") format("embedded-opentype"), url("../../fonts/ProximaNova/ProximaNova-Bold.woff") format("woff"), url("../../fonts/ProximaNova/ProximaNova-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
hr {
  margin: 0;
  font-weight: 400;
}

a {
  display: inline-block;
}

.header {
  border-bottom: 1px solid #f2f2f2;
  width: 100%;
  position: relative;
}

.header_wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 92px;
  transition: all .3s;
}

.header__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffe05d;
  margin-right: 15px;
  margin-bottom: -1px;
  padding-left: 25px;
  padding-right: 25px;
  box-sizing: border-box;
  height: 100%;
}

.nav_wrap {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  z-index: 10;
  height: 100%;
  transition: all .3s;
}

.nav_wrap.active {
  display: flex;
  position: fixed;
  right: 0px;
  left: 0;
  top: 70px;
  bottom: 0;
  flex-direction: column;
  align-items: flex-end;
  background-color: rgba(0, 0, 0, 0.8);
  height: auto;
}

.nav_wrap.active .nav {
  flex-direction: column;
  padding: 35px 20px;
  width: 251px;
}

.nav_wrap.active .nav__link {
  padding: 20px 28px 15px;
  justify-content: flex-start;
  line-height: 16px;
}

.nav_wrap.active .call {
  padding: 0;
  margin: 0;
  width: 251px;
}

.nav_wrap.active .call__number {
  width: 100%;
  text-align: center;
  line-height: 65px;
  padding: 0 20px;
  font-size: 12px;
}

.nav_wrap.active .call__number::before {
  display: inline-block;
}

.nav_wrap.active .call__request_wrap {
  width: 100%;
  height: 45px;
  background-color: #ffe05d;
  margin-top: 0;
}

.nav_wrap.active .call__request {
  line-height: 16px;
}

.nav_wrap.active .call__icon {
  width: 20px;
  height: 20px;
  border-radius: 0;
  background-size: contain;
}

.nav {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: nowrap;
  background-color: #fff;
  transition: all .3s;
  margin: 0;
}

.nav__item {
  width: 100%;
  position: relative;
}

.nav__item:hover .submenu {
  display: block;
}

.nav__link {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  font-weight: 300;
  color: #3c3c3c;
  text-decoration: none;
  position: relative;
  flex-grow: 1;
  text-align: center;
  padding: 0 10px;
  height: 100%;
}

.nav__link::before {
  content: '';
  display: inline-block;
  height: 3px;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: -1px;
  background-color: #ffe05d;
  opacity: 0;
  transition: all .2s;
}

.nav__link_sub::after {
  font-family: FontAwesome;
  content: '\f107';
  font-size: 20px;
  margin-left: 20px;
}

.nav__link_sub:hover::after {
  content: '\f106';
}

.nav__link:hover {
  font-weight: 700;
  color: #3d3d3d;
}

.nav__link:hover::before {
  opacity: 1;
}

.nav .submenu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin: 0;
  background-color: #ededed;
  padding: 15px 0;
  border-bottom: 1px solid #fff6ce;
  display: none;
}

.nav .submenu__link {
  font-size: 14px;
  font-family: 'proximaNova', sans-serif;
  font-weight: 300;
  line-height: 18px;
  padding-left: 54px;
  padding-top: 10px;
  text-decoration: none;
  color: #313131;
}

.call {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 25px;
  padding-right: 25px;
  background-color: #f4f4f4;
  margin-left: 15px;
  box-sizing: border-box;
  transition: all .3s;
}

.call__number {
  font-size: 20px;
  color: #313131;
  text-decoration: none;
  line-height: 15px;
}

.call__number::before {
  content: '';
  display: none;
  width: 15px;
  height: 26px;
  background-image: url(../img/phone.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 10px;
  position: relative;
  top: 9px;
}

.call__request_wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}

.call__icon {
  display: inline-block;
  width: 27px;
  height: 27px;
  background-color: #ffe05d;
  border-radius: 50%;
  background-image: url(../img/tel.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50%;
  margin-right: 10px;
}

.call__request {
  font-size: 15px;
  font-weight: 700;
  color: #242424;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  text-align: center;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
}

.call__request:hover {
  border-bottom: 1px dashed;
}

.burger-menu {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 25px;
  height: 18px;
  cursor: pointer;
}

.burger-menu span {
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: #000;
  transition: all .3s;
}

.burger-menu.active span:nth-child(1) {
  transform: rotateZ(45deg) translate(3px, 3px);
}

.burger-menu.active span:nth-of-type(2) {
  transform: rotateZ(-45deg) translate(3px, -3px);
}

body.fixed {
  overflow: hidden;
}

.modal {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 100;
  align-items: center;
  justify-content: center;
  display: none;
}

.modal__request {
  border: 2px solid #fee05c;
  padding: 50px 40px;
  background-color: #fff;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.5);
  position: relative;
}

.modal__form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 380px;
}

.modal__input, .modal__textarea {
  border: 1px solid #d0d0d0;
  margin-bottom: 20px;
  height: 45px;
  box-sizing: border-box;
  padding-left: 30px;
  line-height: 45px;
  font-size: 16px;
  font-weight: 700;
}

.modal__input::placeholder, .modal__textarea::placeholder {
  opacity: 0.3;
}

.modal__textarea {
  height: 105px;
  resize: none;
}

.modal__rooms {
  margin-bottom: 20px;
}

.modal__input_check {
  display: none;
}

.modal__rooms_number {
  height: 23px;
  width: 23px;
  display: inline-block;
  border: 1px solid #d0d0d0;
  background-color: #f5f5f5;
  font-size: 14px;
  text-align: center;
  line-height: 23px;
  color: #c1c1c1;
  transition: all .2s;
  margin-left: 12px;
}

.modal__rooms_title {
  font-size: 16px;
  line-height: 15px;
  color: #000;
  font-weight: 700;
  margin-right: 20px;
}

.modal label input[type="checkbox"]:checked + .modal__rooms_number {
  background: #fee05c;
  color: #0d0d0d;
  border-color: transparent;
}

.modal__btn {
  border: none;
  line-height: 48px;
  font-size: 14px;
  color: #2d2c2c;
  font-weight: 700;
  background-color: #fee05c;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.modal .close {
  font-size: 50px;
  line-height: 24px;
  font-weight: 300;
  color: #2d2c2c;
  text-decoration: none;
  position: absolute;
  right: -15px;
  top: -28px;
  z-index: 10;
  cursor: pointer;
}

.modal .close i {
  position: relative;
  z-index: 12;
}

.modal .close i.fa-angle-right {
  margin-right: -5px;
}

.modal .close i.fa-angle-left {
  margin-left: -3px;
}

.modal .close::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  top: 26px;
  right: -64px;
  border: 78px solid transparent;
  border-top-color: #fee05c;
  z-index: 9;
}

.modal .close::after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  right: -64px;
  top: -130px;
  border: 78px solid transparent;
  border-bottom-color: #fee05c;
  z-index: 9;
}

.modal.active {
  display: flex;
}

.news {
  padding-top: 70px;
  padding-bottom: 75px;
  border-bottom: 4px solid #edcc3e;
}

.news_wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 985px;
  margin-left: auto;
  margin-right: auto;
}

.news__nav {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 40px;
}

.news__link {
  font-size: 14px;
  font-weight: 300;
  color: #edcc3e;
  text-decoration: none;
  position: relative;
  padding-right: 20px;
}

.news__link::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  top: 8px;
  right: 5px;
  border: 5px solid transparent;
  border-top-color: #edcc3e;
}

.news__link::after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  right: 5px;
  top: -2px;
  border: 5px solid transparent;
  border-bottom-color: #edcc3e;
}

.news__link:nth-child(1)::before {
  border-top-color: #c9c9c9;
}

.news__link:nth-child(1)::after {
  border-bottom-color: #c9c9c9;
}

.news__current {
  font-size: 14px;
  color: #979797;
}

.news__title {
  font-size: 50px;
  line-height: 60px;
  color: #1f1f1f;
  font-weight: 700;
  max-width: 890px;
  margin-bottom: 50px;
}

.news__info {
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  color: #000;
  margin-bottom: 35px;
}

.collapse {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.collapse__item {
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 20px;
  padding-bottom: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 180%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 280px;
  width: 33%;
  position: relative;
  z-index: 1;
  transition: all .3s;
  text-decoration: none;
  margin-bottom: 5px;
}

.collapse__item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.collapse__item:hover {
  background-size: 110%;
}

.collapse__item_hide {
  display: none;
}

.collapse__item_hide.show {
  display: block;
}

.collapse__title {
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  color: #fff;
  position: relative;
  z-index: 5;
}

.collapse__date {
  font-size: 14px;
  font-weight: 300;
  color: #ffe05d;
  position: relative;
  z-index: 5;
}

.collapse__btn {
  border: none;
  background: none;
  font-size: 16px;
  line-height: 22px;
  color: #1d1d1d;
  text-transform: uppercase;
  cursor: pointer;
  display: block;
  margin-left: auto;
  margin-right: auto;
  outline: none;
  transition: all .3s;
}

.collapse__btn:hover {
  transform: scale(1.1);
}

.slider {
  max-width: 1614px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  padding-bottom: 100px;
}

.slider__item {
  text-align: center;
}

.slider__title {
  font-family: 'proximaniva', sans-serif;
  font-size: 15px;
  font-weight: 700;
  color: #000;
  padding: 35px 80px 35px 40px;
  background-color: #ffe05d;
  position: absolute;
  top: 235px;
  left: 175px;
  max-width: 315px;
  text-align: left;
  display: inline-block;
  opacity: 0;
}

.slider__title::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  top: 70px;
  left: 0px;
  border: 50px solid transparent;
  border-bottom-color: #fff;
  z-index: 10;
}

.slider .owl-stage {
  display: flex;
  align-items: center;
}

.slider .owl-item:not(.center) {
  height: 298px;
  overflow: hidden;
}

.slider .owl-item:not(.center)::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.85);
}

.slider .owl-item.center {
  z-index: 100;
  height: 404px;
  overflow: hidden;
}

.slider .owl-item.center .slider__title {
  opacity: 1;
}

.slider .owl-nav {
  position: absolute;
  top: calc(50% - 60px);
  left: calc(50% - 450px);
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 900px;
}

.slider .owl-nav button.owl-prev,
.slider .owl-nav button.owl-next {
  color: #e7d375;
  font-size: 80px;
  line-height: 80px;
  font-weight: 300;
}

.slider .owl-dots {
  text-align: center;
  margin-top: 30px;
}

.slider .owl-dots .owl-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid #d1d1d1;
  margin-right: 18px;
}

.slider .owl-dots .owl-dot.active {
  border-color: transparent;
  background-color: #edcc3e;
}

.footer {
  padding-top: 20px;
}

.footer__nav_wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
}

.footer__logo {
  margin-bottom: 20px;
}

.footer__nav {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  max-width: 740px;
  width: 100%;
  margin: 0;
}

.footer__link {
  font-size: 17px;
  line-height: 32px;
  font-family: 'ProximaNova', sans-serif;
  text-decoration: none;
  color: #3a3a3a;
}

.footer__link:hover {
  border-bottom: 1px solid #ffe05d;
}

.footer__copyright {
  font-family: 'ProximaNova', sans-serif;
  color: #000;
  font-size: 14px;
  margin-bottom: 10px;
  display: inline-block;
  width: 100%;
}

.footer__dev {
  font-size: 17px;
  color: #3f3f3f;
  display: inline-block;
  position: absolute;
  right: 0;
  bottom: 15px;
}

.footer__MediaLine {
  font-size: 17px;
  color: #ffe05d;
  font-weight: 300;
  line-height: 31px;
  padding: 0 12px;
  background-color: #1e1e1e;
  text-decoration-skip-ink: none;
  border-radius: 1px;
  display: inline-block;
}

/**********************
* MEDIA
**********************/
/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {
  .collapse__item {
    height: 240px;
  }
  .slider {
    padding-bottom: 50px;
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .container {
    max-width: 970px;
  }
  .header_wrap {
    height: 80px;
  }
  .header__logo {
    padding: 0 15px;
    min-width: 133px;
  }
  .call {
    padding: 0 15px;
    min-width: 160px;
  }
  .call__number {
    font-size: 14px;
  }
  .nav__link {
    font-size: 14px;
  }
  .news {
    padding: 60px 0;
  }
  .news__nav {
    margin-bottom: 30px;
  }
  .news__title {
    font-size: 42px;
    line-height: 50px;
    margin-bottom: 30px;
  }
  .news__info {
    margin-bottom: 20px;
  }
  .collapse__item {
    height: 200px;
    padding: 20px 20px;
  }
  .footer__nav {
    max-width: 550px;
  }
  .slider {
    padding-bottom: 30px;
  }
  .slider .owl-nav {
    max-width: 90%;
    left: 5%;
  }
  .slider .owl-carousel .owl-item.center,
  .slider .owl-carousel .owl-item {
    height: 350px;
  }
  .slider .slider__title {
    top: 180px;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .container {
    max-width: 750px;
  }
  .header {
    background-color: #ffe05d;
  }
  .header_wrap {
    height: 70px;
    justify-content: space-between;
  }
  .nav_wrap {
    display: none;
  }
  .nav .nav__link {
    height: auto;
  }
  .nav .nav__link:hover::before {
    height: 1px;
    background-color: #f2e9c2;
  }
  .burger-menu {
    display: flex;
  }
  .collapse__item {
    height: 160px;
    padding: 10px;
  }
  .collapse__title {
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 5px;
  }
  .news {
    padding: 50px 0;
  }
  .news__title {
    line-height: 44px;
  }
  .footer__nav {
    max-width: 440px;
  }
  .footer__link {
    font-size: 14px;
  }
  .footer__dev {
    font-size: 14px;
    bottom: 5px;
  }
  .footer__MediaLine {
    font-size: 14px;
    padding: 0 5px;
    line-height: 28px;
  }
  .modal__request {
    padding: 60px 50px;
  }
  .modal__form {
    width: 300px;
  }
  .modal__input,
  .modal__textarea {
    margin-bottom: 20px;
    padding-left: 15px;
  }
  .modal__rooms {
    margin-bottom: 20px;
  }
  .modal__btn {
    line-height: 45px;
  }
  .modal .close {
    right: -12px;
    top: -22px;
  }
  .modal .close::before {
    border-width: 36px;
    right: -26px;
    top: 21px;
  }
  .modal .close::after {
    right: -26px;
    top: -50px;
    border-width: 36px;
  }
  .modal .close i {
    font-size: 36px;
  }
  .modal .close i.fa.fa-angle-right {
    margin-right: -2px;
  }
  .nav .submenu {
    position: relative;
    top: 0;
  }
  .slider .owl-carousel .owl-item.center,
  .slider .owl-carousel .owl-item {
    height: 300px;
  }
  .slider .slider__title {
    font-size: 14px;
    padding: 20px 40px 20px 25px;
    top: 182px;
    left: 100px;
  }
  .slider .slider__title::before {
    border-width: 30px;
    top: 58px;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 576px) {
  .collapse__item {
    width: 49%;
  }
  .collapse__title {
    font-size: 14px;
    line-height: 18px;
  }
  .footer__nav_wrap {
    justify-content: space-around;
  }
  .footer__nav {
    width: auto;
    flex-direction: column;
    margin-bottom: 10px;
  }
  .footer__link {
    line-height: 24px;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  .header_wrap {
    height: 50px;
    justify-content: space-between;
  }
  .header__logo {
    min-width: 50px;
    padding-left: 5px;
  }
  .header__logo img {
    max-height: 30px;
  }
  .nav_wrap.active {
    top: 50px;
  }
  .news {
    padding: 30px 0;
  }
  .news__nav {
    margin-bottom: 20px;
  }
  .news__title {
    font-size: 28px;
    line-height: 36px;
    text-align: center;
    margin-bottom: 20px;
  }
  .news__info {
    margin-bottom: 15px;
  }
  .collapse__item {
    height: 120px;
  }
  .footer__dev {
    font-size: 8px;
  }
  .footer__MediaLine {
    font-size: 10px;
  }
  .modal__request {
    padding: 25px 15px;
  }
  .modal__form {
    width: 220px;
  }
  .modal__input {
    margin-bottom: 10px;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
  }
  .modal__rooms {
    margin-bottom: 10px;
  }
  .modal__rooms_title {
    margin-right: 5px;
    font-size: 12px;
  }
  .modal__rooms_number {
    margin-left: 7px;
    width: 18px;
    height: 18px;
    line-height: 18px;
  }
  .modal__textarea {
    height: 90px;
    font-size: 14px;
  }
  .slider .owl-carousel .owl-item.center,
  .slider .owl-carousel .owl-item {
    height: 220px;
  }
  .slider .slider__title {
    font-size: 12px;
    padding: 15px 25px 15px 20px;
    top: 140px;
    left: 70px;
    max-width: 270px;
  }
  .slider .slider__title::before {
    border-width: 24px;
    top: 32px;
  }
}
